@font-face {
    font-family: miaowu;
    src: url('方正-喵呜.TTF');
}

* {
    margin: 0;
    padding: 0;
}

.Login_Container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: miaowu;
    /*background: linear-gradient(45deg, rgb(200, 176, 250), rgb(245, 189, 253)) fixed;*/
    background: linear-gradient(60deg, rgb(78, 82, 246), rgb(145, 165, 248)) fixed;
}

.Login_Container .container {
    position: relative;
    width: 70rem;
}

.Login_Container .container img {
    width: 70rem;
}

.Login_Container .switch span {
    color: #ccc;
    font-size: 1.4rem;
    cursor: pointer;
    font-family: miaowu;
}

.Login_Container .switch span.active {
    color: rgb(116, 67, 226);
}

.Login_Container .panel {
    width: 30%;
    margin: 10rem 0 0;
    position: absolute;
    right: 0;
    top: 0;

    display: flex;
    justify-content: center;
}

.Login_Container .form {
    width: 12rem;
    margin: 3rem 0 0;
}

.Login_Container .form .input {
    position: relative;
    opacity: 1;
    height: 2rem;
    width: 100%;
    margin: 2rem 0;
    transition: .4s;
}

.Login_Container .input input {
    outline: none;
    width: 100%;
    border: none;
    border-bottom: .1rem solid rgb(181, 154, 254);
    position: relative;
    line-height: 35px;
    background: transparent;
    z-index: 1;
}

.Login_Container .input label {
    position: absolute;
    left: 0;
    top: 20%;
    font-size: 1.2rem;
    color: rgb(129, 101, 207);
    transition: .3s;
    font-family: miaowu;
}   

/* fixbug for IMBIT（1448214956） */
.Login_Container .hasValue ~ label,.Login_Container  input:focus ~ label {
    top: -50%;
    font-size: .9rem;
}



.Login_Container .form span {
    display: block;
    color: rgb(110, 89, 167);
    font-size: .8rem;
    cursor: pointer;
}

.Login_Container .form button {
    border: none;
    outline: none;
    margin: 2.5rem 0 0;
    width: 100%;
    height: 3rem;
    border-radius: 3rem;
    background: linear-gradient(90deg, rgb(102, 107, 255), rgb(152, 130, 253));
    /*background: linear-gradient(90deg, rgb(181, 154, 254), rgb(245, 189, 253));*/
    box-shadow: 0 0 8px rgb(181, 154, 254);
    cursor: pointer;
    color: white;
    font-family: miaowu;
}

